<template>
    <div class='video_study'>
        <div class="header">
            <div class="back"  @click.stop="back()">
                <img src="../../assets/image/联合42.png" alt="">
            </div>
            <div class="video_study_head">视听学习</div>
            <div class='search' @click='enterSearch()'>
                <img src="../../assets/image/search.png" alt="">
            </div>
        </div>
        <v-page class="plan-list-page" style="font-size: 0.3rem;">
            <mt-loadmore ref="loadmore" class="loadmore"
                        :top-method="loadTop" v-infinite-scroll="loadBottom" infinite-scroll-disabled="moreLoading01" infinite-scroll-distance="40" infinite-scroll-immediate-check="false" :autoFill="false">
                <div class="activity-list" ref="scrollContainer">
                    <div class='videoStudy_item' v-for='(item,index) in journalismData' :key='item.code'>
                        <div class='videoStudy_top'>
                            <video-player  class="vjs-custom-skin video-player"
                                    ref="videoPlayer"
                                    :options="playerOptions[index]"
                                    :playsinline="true"
                                    name='video'
                                    @play="onPlayerPlay($event,item.id)"
                                    :events="events"
                                    @fullscreenchange="onPlayerFullScreenchange($event)"
                                    @timeupdate="onPlayerTimeupdate($event)"
                                    @pause="onpause($event)"
                                    >
                            </video-player>
                        </div>
                        <div class='videoStudy_bottom' @click.stop='enterVideoDetail(item.id,item.specialCode)'>
                            <div class='videoStudy_name'>
                                {{item.name}}
                            </div>
                            <div class='videoStudy_visit'>
                                浏览 {{item.viewNum}}
                            </div>
                        </div>
                    </div>
                    <div class="more_loading" style="height:0.30rem; line-height:0.30rem; text-align:center" v-show="!queryLoading01">
                        <div type="snake" color="#00ccff" :size="20" v-show="moreLoading01 && !allLoaded01">加载中...</div>
                        <div v-show="allLoaded01" style="color:#ccc">---- 看到底线了 ----</div>
                    </div>
                </div>
            </mt-loadmore>
            <div class="list-empty-show" v-if="isEmpty">
                暂无数据
            </div>
        </v-page>
        <div class='empty'>
            <img src="../../assets/image/空空如也.png" alt="">
            <div class='emptyWord'>空空如也~</div>
        </div>
        <!-- 加载页面 -->
        <Loading :dialogVisible="verDialogVisible"></Loading>
    </div>
</template>
<script>
import Loading from './../components/loading'
import API from '@/api/api'
import $ from 'jquery'
import { Indicator } from 'mint-ui'
export default {
  name: 'video_study',
  components: {
    Loading
  },
  data () {
    return {
      events: ['fullscreenchange'],
      specialCode: '', // 专项类型
      journalismData: [], // 视听学习列表
      playerOptions: [], // 视频属性
      isEmpty: false, // 列表查询结果是否为空
      allLoaded: false, // 列表数据是否全部加载
      topStatus: false, // 顶部下拉刷新
      queryLoading01: false,
      moreLoading01: false,
      allLoaded01: false,
      pageSize: 10,
      pageNum: 1,
      pageCount: 100,
      verDialogVisible: true,
      ifFullscreen: false,
      addTime: 0, // 时间增长
      spendTime: 0 // 时间改变时获取的当前播放的时间
    }
  },
  created () {
    this.specialCode = localStorage.getItem('dataListSpecialCode')
    this.getSelectMateAndSche('down')
  },
  computed: {
    player () {
      return this.$refs.videoPlayer.player
    }
  },
  watch: {
    spendTime (val, oldval) {
      this.addTime++
    },
    addTime (val) {
      console.log(val)
      if (val % 300 === 0) {
        this.addSelfCredits()
      }
    },
    // 全屏按钮
    ifFullscreen (val, oldval) {
      if (val) {
        screen.orientation.lock('landscape')
        // eslint-disable-next-line no-undef
        StatusBar.hide()
      } else {
        screen.orientation.lock('portrait')
        // eslint-disable-next-line no-undef
        StatusBar.show()
      }
    }

  },

  methods: {
    onpause (player) {
      this.addTime--
    },
    onPlayerPlay (player, id) {
      API.post('findVideoDetilsById', {
        deflag: '0',
        id: id
      }, 'json', (data, err) => {
        if (data.success) {
          if (data.data) {
            this.addTime++
            var video = document.getElementsByTagName('video')
            // eslint-disable-next-line no-inner-declarations
            function pauseAll () {
              let self = this;
              [].forEach.call(video, function (i) {
                // 将audios中其他的audio全部暂停
                i !== self && i.pause()
              })
            }
            [].forEach.call(video, function (i) {
              i.addEventListener('play', pauseAll.bind(i))
            })
          } else {
            this.$toast('该视频不存在')
            this.getSelectMateAndSche('down')
          }
        }
      })
    },
    onPlayerTimeupdate (player) {
      this.spendTime = parseInt(player.currentTime())
    },
    addSelfCredits () {
      // API.post('addSelfCredits', {
      //   specialCode:this.specialCode,
      //   creditsId:'learnTime'
      // }, 'json',(data,err)=>{
      //   if(data.success){
      //     console.log(data)
      //   }
      // })
    },
    // 全屏按钮
    onPlayerFullScreenchange (player) {
      this.ifFullscreen = player.isFullscreen()
    },
    // 返回上一路由
    back () {
      this.$router.back()
    },
    // 进入搜索
    enterSearch () {
      localStorage.setItem('dataListSpecialCode', this.specialCode)
      localStorage.setItem('enterIndex', this.GLOBAL.ENTERSOURCE.videoSearch)
      this.$router.push({
        name: 'search'
      })
    },
    // 下拉刷新
    loadTop () {
      this.pageNum = 1
      this.allLoaded = false
      Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
      this.getSelectMateAndSche('down')
      this.$refs.loadmore.onTopLoaded()
    },
    // 上拉加载
    loadBottom () {
      if (this.allLoaded01) {
        this.moreLoading01 = true
        return
      }
      if (this.queryLoading01) {
        return
      }
      this.moreLoading01 = !this.queryLoading01
      setTimeout(() => {
        this.pageNum++
        this.getSelectMateAndSche('up')
      }, 500)
    },
    // 获取内容
    getSelectMateAndSche (flag) {
      const that = this
      // 获取此专项类型下的视频
      this.queryLoading01 = false
      this.moreLoading01 = false
      this.allLoaded01 = false
      API.post('selectMateAndSche', {
        specialCode: this.specialCode,
        type: '1202',
        pageSize: this.pageSize,
        pageNum: this.pageNum
      }, 'json', (data, err) => {
        if (data.success) {
          if (flag === 'down') {
            this.journalismData = []
            this.playerOptions = []
          };
          this.pageCount = data.data.total
          this.forBackData(data.data.records)
          that.verDialogVisible = false
        } else {
          alert(data.msg)
          this.verDialogVisible = false
        }
      })
    },
    // 循环返回的数据
    forBackData (list) {
      for (var i = 0; i < list.length; i++) {
        let arrStr = {
          autoplay: false,
          controls: true, // 控制条
          muted: false,
          language: 'zh-CN',
          fluid: true,
          //playbackRates: [0.7, 1.0, 1.5, 2.0],
          aspectRatio: '16:9',
          preload: 'auto', // 视频预加载
          sources: [{
            type: 'application/x-mpegURL',
            // type: "video/mp4",
            src: list[i].url
            // src: "http://vjs.zencdn.net/v/oceans.mp4",
          }],
          poster: list[i].imageUrl,
          // poster:'http://152.136.158.153:8080/pxks/dl/3d7b28eb971f176e9a0ae2e8f9d5754c25a8413e537bff1f754c1fe31e1a6589',
          notSupportedMessage: '此视频暂无法播放，请稍后再试', // 允许覆盖Video.js无法播放媒体源时显示的默认信息。
          controlBar: {
            timeDivider: true,
            durationDisplay: true,
            remainingTimeDisplay: false,
            fullscreenToggle: true // 全屏按钮
          }
        }
        this.playerOptions.push(arrStr)
        // 解析考试信息
        this.journalismData.push(list[i])
      }
      // this.pageCount = this.journalismData.length;
      // console.log(this.journalismData)
      this.isEmpty = false
      this.allLoaded01 = this.journalismData.length === this.pageCount || list.length === 1
      // console.log(this.journalismData.length);
      this.moreLoading01 = this.allLoaded01
      Indicator.close()
      if (this.journalismData.length === 0) {
        $('.empty').css('display', 'flex')
        $('.plan-list-page').css('display', 'none')
      } else {
        $('.empty').css('display', 'none')
        $('.plan-list-page').css('display', 'block')
      }
    },
    // 进入视频详情页
    enterVideoDetail (id, specialCode) {
      API.post('findVideoDetilsById', {
        deflag: '0',
        id: id
      }, 'json', (data, err) => {
        if (data.success) {
          if (data.data) {
            this.$router.push({
              name: 'video_detail',
              params: { id: id, specialCode: specialCode }
            })
          } else {
            this.$toast('该视频不存在')
            this.loadTop()
          }
        }
      })
    }
  }
}
</script>
<style scoped>
    .video_study{
        width:100%;
        height:100vh;
        background:#fff;
    }
    .video_study .header{
        width:100%;
        background:#fff;
        font-size:0.36rem;
        font-family: PingFang-SC-Medium;
        position: fixed;
        top:0;
        height: 0.94rem;
        display: flex;
        justify-content: center;
        z-index: 99;
    }
    .video_study .back{
        width:0.89rem;
        height:0.94rem;
        position: fixed;
        top:0;
        left:0;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 100;
    }
    .video_study .back img{
        width:0.19rem;
        height:0.34rem;
    }
    .video_study .search{
        width:1.11rem;
        height: 0.94rem;
        position: fixed;
        top:0;
        right:0;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 99;
    }
    .video_study .search img{
        width:0.41rem;
        height:0.41rem;
    }
    .video_study_head{
        line-height: 0.94rem;
        font-size:0.38rem;
        color:#4E4E4E;
        width:3rem;
        overflow-x:hidden;
        text-overflow:ellipsis;
        padding-top:0.02rem;
        white-space:nowrap;
        text-align: center
    }
    .activity-list{
        background: #fff;
        padding:0 0.35rem;
        /* margin-top:0.94rem; */
        width:100%;
        height:calc(100vh - 0.94rem);
        /* padding-top:1rem; */
        width:100%;
    }
    .activity-list .videoStudy_item{
        padding:0.3rem 0 0.36rem 0;
        display: flex;
        flex-direction: column;
        border-top:0.012rem solid rgba(243,243,243,1);
    }
    .activity-list .videoStudy_item:first-child{
        padding-top:0.12rem;
        border-top:0;
    }
    .videoStudy_item .videoStudy_top{
        width:100%;
        margin-bottom: 0.13rem;
        border-radius: 0.06rem;
    }
    .videoStudy_item .videoStudy_top img{
        width:100%;
        height:3.84rem;
        display: block;
    }
    .videoStudy_item .videoStudy_bottom{
        width:100%;
    }
    .videoStudy_item .videoStudy_name{
        font-size: 0.4rem;
        color:#384150;
        font-weight: bold;
        margin-bottom: 0.12rem;
        /* justify-content: flex-start; */
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        word-break: break-word;
        /* height:0.75rem; */
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        line-height: 0.57rem;
        /* width: calc(100vw - 4.3rem); */
        /*! autoprefixer: off */
         /* autoprefixer: on */
    }
    .videoStudy_item .videoStudy_visit{
        font-size:0.26rem;
        color:#9FA7AF;
    }
    .empty{
        margin-top:2.75rem;
        display: none;
        flex-direction: column;
        width:100%;
        align-items: center;
    }
    .empty img{
        width:2.91rem;
        height:2.63rem;
    }
    .empty .emptyWord{
        margin-top:0.4rem;
        color:#9FA7AF;
        font-size:0.32rem;
    }
    .mint-loadmore{
        max-height: calc(100vh - 0.94rem);
        overflow-y: auto;
        overflow-x:hidden;
        margin-top: 0.94rem;
        background: #fff;
        /* -webkit-overflow-scrolling: touch; */
    }
</style>



